import axios from 'axios'
import store from '../../store/index.js'
import {
    openidLogin
} from "./apis.js"

//设置默认请求头
axios.defaults.headers['Content-Type'] = 'application/json;charset=utf-8'

//根据环境不同，设置baseURL
let baseURL
if (process.env.NODE_ENV == 'development') { // 测试环境
    baseURL = 'https://app.newtiming.com/api/v1';
} else { // 正式环境        
    baseURL = 'https://app.newtiming.com/api/v1';
}

//创建axios实例，并将baseurl传入，设置baseURL的该实例默认值
const service = axios.create({
    // axios中请求配置有baseURL选项，表示请求URL公共部分
    baseURL: baseURL
})

//请求拦截器，在请求发起之前进行拦截，并处理该次请求的携带信息
service.interceptors.request.use(config => {
    console.log("请求拦截器")
    // console.log(config)

    //如果请求地址不是获取验证码，登录，获取微信授权，详情页获取数据，获取则将已经获得的请求token设置到本次请求信息中
    try {
        if (config.token || config.params.token) {
            config.headers.token = store.state.appUserInfo.token
        }
    } catch (e) {
        console.log(e)
    }

    return config
})

var requestUrl = '',
    methods = '',
    requestData = {},
    requestParams = {},
    resData = {};

//响应拦截器，在相应数据被回调函数处理之前进行拦截，并处理该次请求的返回信息
service.interceptors.response.use(response => {
    console.log("响应拦截器")
    // console.log(response)
    if (response.data.status == "out_of_date") {
        requestUrl = response.config.url
        methods = response.config.method
        requestData = response.config.data
        requestParams = response.config.params
        const data = {
            openid: store.state.WXUserInfo.open_id
        }
        return openidLogin(data).then(res => {
            store.commit("setAppUserInfo", {
                token: res.token,
                id: res.data.id,
            });
            if (methods == "get") {
                return service({
                    method: methods,
                    params: requestParams,
                    url: requestUrl,
                    token: true
                }).then(res => {
                    return resData = res
                })
            } else if (methods == "post") {
                return service({
                    method: methods,
                    data: requestData,
                    url: requestUrl,
                    token: true
                }).then(res => {
                    return resData = res
                })
            }
        })
    } else {
        return response.data
    }


})

export default service