import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routes'
import store from '../store/index'
import {
  openidLogin
} from '../assets/javascript/apis.js'
Vue.use(VueRouter)

//解决使用this.$router.push,跳转向同一个页面会发生报错的问题
//同时能够解决使用this.$router.push,跳转时发生重定向报错的问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

//声明vue-router的实例化对象
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    };
  }
})

//从地址栏获取参数的函数
let getUrlParam = (name) => {
  let urlParam = decodeURI(window.location.search).substr(1).split("&");
  for (let i = 0; i < urlParam.length; i++) {
    if (urlParam[i].split("=")[0] == name) {
      return urlParam[i].split("=")[1];
    }
  }
  return false;
}
let testOpenId = false
//路由全局前置守卫
router.beforeEach((to, from, next) => {

  let ac_id = getUrlParam("ac_id"),
    from_user_id = getUrlParam("from_user_id"),
    ParamOpenId = getUrlParam("open_id"),
    openId = store.state.WXUserInfo.open_id;

  if (ac_id && from_user_id && Number(ac_id) && Number(from_user_id)) {
    store.commit("setDistributionInfo", {
      ac_id: Number(ac_id),
      from_user_id: Number(from_user_id),
    });
  }

  if (!testOpenId) {
    if (openId) {
      console.log(to.meta.isOutSide)
      if (!to.meta.isOutSide) {
        openidLogin({
          openid: store.state.WXUserInfo.open_id
        }).then(res => {
          console.log(res)
          store.commit("setAppUserInfo", {
            token: res.token,
            id: res.data.id,
          });
          if (res.status == "none") {
            if (to.name == "login") {
              next()
            } else {
              next("login")
            }
          } else {
            testOpenId = true
            next()
          }
        })
      } else {
        next()
      }
    } else if (ParamOpenId) {

      //获取微信授权信息
      let open_id = getUrlParam("open_id"),
        name = getUrlParam("name"),
        avatar = getUrlParam("avatar");

      //设置微信用户信息
      store.commit("setWXUserInfo", {
        open_id,
        name,
        avatar,
      })
      if (!to.meta.isOutSide) {
        openidLogin({
          openid: store.state.WXUserInfo.open_id
        }).then(res => {
          console.log(res)
          store.commit("setAppUserInfo", {
            token: res.token,
            id: res.data.id,
          });
          if (res.status == "none") {
            if (to.name == "login") {
              next()
            } else {
              next("login")
            }
          } else {
            testOpenId = true
            next()
          }
        })
      } else {
        next()
      }
    } else {
      let pureUrl = encodeURIComponent(window.location.origin + to.path);
      location.replace("https://app.newtiming.com/api/v1/socials/authorizations?redirect_url=" + pureUrl);
    }
  } else {
    next()
  }

})

//路由全局后置守卫
router.afterEach((to, from) => {
  document.title = to.meta.title
})

export default router